.sidebar {
    padding: 16px 24px;
    position: absolute;
    top: 0;
    left: -100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    transition: all ease 0.3s;
    border: 1px solid #bdb9b9;

}

.sidebar.open {
    top: 0;
    left: 0;
    z-index: 50;

}

.background {
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    padding: 0 46px;


}

.nav a {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color: #000;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
    padding: 0 46px;
}

.close {
    background-color: inherit;
    border: none;
    cursor: pointer;
    position: relative;
    text-align: right;
}

